.section-book {
  padding: 15rem 0;
  background-image: linear-gradient(
    to right bottom,
    $color-tertiary-dark,
    $color-primary-dark
  );

  .book {
    background-image: linear-gradient(
      105deg,
      rgba($color-white, 0.9) 50%,
      transparent 50%
    );
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 3px;
    box-shadow: 0 1.5rem 4rem rgba($color-black, 0.2);

    height: 50rem;

    &__form {
      width: 50%;
      padding: 6rem;
    }
  }
}
